.header {
  background-color: #003580;
  color: white;
  display: flex;
  justify-content: center;
  position: relative;
}

.headerContainer {
  width: 100%;
  max-width: 1024px;
  margin: 20px 0px 100px 0px;
}

.headerContainer.listMode {
  margin: 20px 0px 0px 0px;
}

.headerList {
  display: flex;
  gap: 40px;
  margin-bottom: 50px;
}

.headerListItem {
  display: flex;
  align-items: center;
  gap: 10px;
}

.headerListItem.active {
  border: 1px solid white;
  padding: 10px;
  border-radius: 20px;
}
.headerTitle{
  
}
.headerDesc {
  margin: 20px 0px;
  font-size:15px;
}

.headerBtn {
  background-color: #0071c2;
  color: white;
  font-weight: 500;
  border: none;
  padding: 10px;
  cursor: pointer;
}

.headerSearch {
  height: 30px;
  background-color: white;
  border: 3px solid #febb02;
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 10px 0px;
  border-radius: 5px;
  position: absolute;
  /* theo vi tri của header */
  bottom: -25px; 
  width: 100%;
  max-width: 1024px;
}

.headerIcon {
  color: rgb(0, 0, 0);
}

.headerSearchItem {
  display: flex;
  align-items: center;
  gap: 10px;
}
.suggestionDestination{
  width: 25%;
  position: absolute;
  top: 50px;
  background-color: #ffffff;
    /* z-index: 2; */
  color: black;
  padding:10px;
  border-radius: 5px;
  z-index: 3;
  box-shadow: 0px 8px 16px 2px rgba(0, 0, 0, 0.2); /* Thêm bóng đổ để nổi lên */
}

.suggestionsList{
 
}
.headerSearchInput {
  border: none;
  outline: none;
 
}

.headerSearchInput::placeholder{
  color: lightgray;
  cursor: pointer;
  font-weight: bold;
}

.headerSearchText {
  color: black;
  cursor: pointer;
  /* font-weight: bold; */
}

.date {
  position: absolute;
  top: 50px;
  z-index: 2;
}

.options {
  z-index: 2;
  position: absolute;
  top: 50px;
  background-color: white;
  color: gray;
  border-radius: 5px;
  -webkit-box-shadow: 0px 0px 10px -5px rgba(0, 0, 0, 0.4);
  box-shadow: 0px 0px 10px -5px rgba(0, 0, 0, 0.4);
}

.optionItem {
  width: 200px;
  display: flex;
  justify-content: flex-start;
  gap:10px;
  align-items: center;
  margin: 10px;
}

.optionCounter {
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 12px;
  color: black;
}

.optionCounterButton {
  width: 30px;
  height: 30px;
  border: 1px solid #0071c2;
  color: #0071c2;
  cursor: pointer;
  background-color: white;
}

.optionCounterButton:disabled {
  cursor: not-allowed;
}