.footer{
  background-color: #003580;
  display: flex;
  justify-content: center;
  width: 100%;
  margin-top: 30px;
}
.footerContainer{
    width: 100%;
    width: 1024px;
    font-size: 12px;
  }
  
  .fLists{
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-bottom: 50px;
  }
  
  .fList{
    list-style: none;
    padding: 0;
  }
  
  .fListItem{
    margin-bottom: 10px;
    color: #ffffff;
    cursor: pointer;
  }