.sidebar {
    flex: 1;
    /* border-right: 0.5px solid #e6e3e3; */
    min-height: 100vh;
    background-color: white;
}
.sidebar .top {
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #003580;
    
}
.sidebar .top .logo {
    font-size: 20px;
    font-weight: bold;
    color: #6439ff;
}
.sidebar hr {
    height: 0;
    /* border: 0.5px solid #e6e3e3; */
}
.sidebar .center {
    padding-left: 10px;
    border-right: 0.5px solid #e6e3e3; 
    min-height: 100vh;
    /* ?????? */

}
.sidebar .center ul {
    list-style: none;
    margin: 0;
    padding: 0;
}
.sidebar .center ul .title {
    /* tiêu đề LIst main */
    font-size: 17px;
    font-weight: bold;
    color: #999;
    /* margin-top: 15px; */
    margin-bottom: 5px;
}
.sidebar .center ul li {
    display: flex;
    align-items: center;
    padding: 5px;
    cursor: pointer;
}
.sidebar .center ul li:hover {
    background-color: #ece8ff;
}
.sidebar .center ul li .icon {
    font-size: 18px;
    color: #7451f8;
}
.sidebar .center ul li span {
    /* chữ */
    font-size: 15px;  
    font-weight: 600;
    color: #888;
    margin-left: 10px;
}
